.sweeper-grid {
  display: grid;
  margin: 0 auto;
  width: 500px;
  grid-template-columns: repeat(10, 50px);
  grid-template-rows: repeat(10, 50px);
  justify-content: stretch;
}
.sweeper-cell {
  text-align: center;
  line-height: 3;
  width: 50px;
  height: 50px;
  border-left: 1px solid #bbc4cc;
  border-bottom: 1px solid #bbc4cc;
}
.sweeper-cols {
  border-top: 1px solid #bbc4cc;
}
.sweeper-cols:last-child > .sweeper-cell {
  border-right: 1px solid #bbc4cc;
}
.sweeper-cell.open {
  background-color: #33383c;
}
.sweeper-container {
  color: white;
  text-align: center;
}
body {
  background-color: #161c23;
  text-align: center;
}
